export default {
	data() {
		return {
			userInfo:{
				phoneNum:'',
				newPhoneNum:''
			},
			smscode:'',
			newsmscode:'',
			codeBtnShow:true,
			codeTimer:null,
			count:'',
			confirmSuccess:false,
		};
	},
	computed:{
		phoneSafe(){
			return this.userInfo.phoneNum.substr(0,3)+"****"+this.userInfo.phoneNum.substr(7);
		}
	},
	onLoad(e) {
		this.userInfo.phoneNum = e.phone;
	},
	methods:{
		getCode(phone,type){
			if(this.codeBtnShow){
				if(this.userInfo.phoneNum == ''){
					uni.showToast({
						title:'手机号不能为空',
						icon:'none'
					})
					return false;
				}
				this.$uniRequest(
					this.$baseUrl+'/v1/sms/send',
					{
						mobile:phone,
						smstype:type
					},
					(res)=>{
						if(res.data.code == 200){
							uni.showToast({
								title:res.data.msg,
								success: () => {
									this.codeBtnShow = false;
									const TIME_COUNT = 60;
									if (!this.codeTimer) {    
										this.count = TIME_COUNT;    
										this.codeBtnShow = false;    
										this.codeTimer = setInterval(() => {    
											if (this.count > 0 && this.count <= TIME_COUNT) {     
												this.count--;     
											} else {     
												this.codeBtnShow = true;     
												clearInterval(this.codeTimer);     
												this.codeTimer = null;     
											}    
										}, 1000)    
									}  
								}
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}
					},
					false
				)
			}
			
		},
		confirm(){
			uni.showLoading({
				title:'验证手机号中...',
				mask:true
			})
			this.$uniRequest(
				this.$baseUrl+'/v1/sms/checkValidate',
				{
					mobile:this.userInfo.phoneNum,
					smstype:4,
					smscode:this.smscode
				},
				(res)=>{
					if(res.data.code == 200){
						uni.showToast({
							title:'手机号验证成功',
							mask:true,
							duration:1000,
							success: () => {
								this.codeBtnShow = true;
								this.confirmSuccess = true;
								clearInterval(this.codeTimer);     
								this.codeTimer = null;
							}
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				},
				false
			)
		},
		changePhone(){
			if(this.userInfo.newPhoneNum.length != 11){
				uni.showToast({
					title:'请输入正确的11位手机号码',
					icon:'none'
				})
				return false;
			}
			uni.showLoading({
				title:'修改中',
				mask:true
			})
			this.$uniRequest(
				this.$baseUrl+'/v1/member/changeMobile',
				{
					mobile:this.userInfo.newPhoneNum,
					smscode:this.newsmscode
				},
				(res)=>{
					if(res.data.code == 200){
						uni.showToast({
							title:'手机号码换绑成功',
							mask:true,
							duration:1000,
							success: () => {
								setTimeout(()=>{
									uni.switchTab({
										url:'/pages/mine/mine'
									})
								},1000)
							}
						})
					}else{
						uni.showToast({
							title:"手机号码修改失败",
							icon:'none'
						})
					}
				}
			)
		}
	}
}
